"🡕")]),_vm._v(".")]),_c('p',[_vm._v("En matière de solidarité, la Ville de Montréal a mis sur place un programme de financement en ce temps de crise pour les petites entreprises montréalaises et les organismes à but non lucratif (OBNL) "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&_dad=portal&_schema=PORTAL&id=32550&ret=http://ville.montreal.qc.ca/pls/portal/url/page/prt_vdm_fr/rep_annonces_ville/rep_communiques/communiques"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(". De plus, la Ville de Montréal est restée solidaire envers ses arrondissements en déployant de l’aide particulièrement dans Montréal-Nord, un des quartiers les plus touchés par la pandémie "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&_dad=portal&_schema=PORTAL&id=32602"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(". Elle a aussi encouragé la réouverture des jardins communautaires face à ses citoyens qui s’assurent d’une sécurité et un soutien alimentaires "),_c('a',{staticClass:"reference-link",attrs:{"href":"http://ville.montreal.qc.ca/portal/page?_pageid=5798,42657625&_dad=portal&_schema=PORTAL&id=32604"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(".")]),_c('h3',[_vm._v("Durant le confinement, l’inclusion urbaine est-elle une préoccupation"+_vm._s('\xa0')+"?")]),_c('p',[_vm._v("L'inclusion urbaine joue un rôle important considérant que ce sont les ménages à plus faible revenu qui sont les plus à risque de contracter la COVID-19, ainsi le gouvernement a inclu davantage d’aides à ces communautés pour éviter que la crise sévisse dans l'entièreté de la société. "),_c('a',{staticClass:"reference-link",attrs:{"href":"https://www.journaldemontreal.com/2020/05/05/la-covid-19-fait-plus-de-ravages-dans-les-quartiers-a-faible-revenu"}},[_vm._v("Réf"+_vm._s('\xa0')+"🡕")]),_vm._v(" "),_c('a',{staticClass:"reference-link",attrs:{"href":"https://www.journaldemontreal.co